html, body, #root, .root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overscroll-behavior: none;
}

.items {
  height: 0;
  flex-grow: 1;
  overflow: auto;
  gap: 8px;
}

.item {
  background-color: #efefef;
  border-radius: 3px;

  & .name {
    font-size: 120%;
    text-transform: capitalize;
  }

  & .days-left {
    position: relative;
    font-size: 16px;
    padding-left: 5px;
    color: rgba(255, 255, 255, 0.8);
  }

  & .p-progressbar {
    background-color: transparent !important;
  }

  & .riskbar {
    height: 20px;
    border-radius: 3px;
  }
}

.p-flex-grow, .p-autocomplete-input {
  flex-grow: 1;
}
